import {
  Service
} from "./service.js"

// 查询钻石消费明细
export function queryConsumeDetails (params, headerData) {
  return Service({
    url: '/api/public/charge/queryConsumeDetails',
    method: 'get',
    headers: {
      Authorization: headerData.token,
      userId: headerData.userId
    },
    params: params
  })
}
